@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans Light";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans Bold";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #283645;
  background-color: #f2f2f2;
}
@media (min-width: 960px) {
  body {
    font-size: 18px;
    line-height: 30px;
  }
}
.img {
  width: 100%;
  height: auto;
}
.visually-hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  position: relative;
}
.page-header__logo {
  height: 66px;
  width: 75px;
  margin-left: 20px;
  position: relative;
}
.page-header__logo svg {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
}
.page-header__logo:hover path {
  fill: #d22856;
}
.page-header__logo:active path {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__logo-tablet {
  display: none;
}
.page-header__logo-desktop {
  display: none;
}
@media (min-width: 660px) {
  .page-header__logo {
    width: 147px;
    height: 109px;
  }
  .page-header__logo svg {
    margin: 27px auto 43px;
  }
  .page-header__logo-mobile {
    display: none;
  }
  .page-header__logo-tablet {
    display: inline-block;
  }
}
@media (min-width: 960px) {
  .page-header {
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__wrapper {
    max-width: 940px;
    margin: 0 auto;
    flex-direction: row;
    align-items: center;
    background-color: transparent;
  }
  .page-header__logo {
    height: 110px;
    margin: 0;
    padding: 0;
  }
  .page-header__logo-tablet {
    display: block;
  }
  .page-header__logo-desktop {
    display: inline-block;
  }
}
.main-nav {
  background-color: #283645;
}
.main-nav--closed {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  text-align: center;
  text-transform: uppercase;
  line-height: 32px;
  background-color: #283645;
}
.main-nav__toggle {
  display: block;
  position: absolute;
  top: 21px;
  right: 20px;
  margin: 0;
  padding: 0;
  width: 50px;
  height: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 30px;
  height: 5px;
  background-color: #f2f2f2;
}
.main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__toggle:active::before,
.main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav__toggle:hover::before,
.main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav__menu--closed .main-nav__list {
  display: none;
}
.main-nav__menu--closed .main-nav__toggle {
  content: "";
  width: 50px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav__menu--closed .main-nav__toggle::before {
  display: none;
}
.main-nav__menu--closed .main-nav__toggle::after {
  display: none;
}
.main-nav__menu--closed .main-nav__toggle:active {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav__menu--closed .main-nav__toggle:hover {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 45px;
    right: 20px;
    margin: 0;
    padding: 0;
  }
}
@media (min-width: 960px) {
  .main-nav {
    background-color: transparent;
  }
  .main-nav__menu {
    width: 100%;
    margin: 0;
    padding: 0;
    margin-left: 220px;
  }
  .main-nav__list {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    background-color: transparent;
    text-transform: none;
  }
  .main-nav__toggle {
    display: none;
  }
}
